
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {mapMutations} from 'vuex'
export default {
  head: {
    title: {
      inner: '我的资料'
    }
  },
  data () {
    return {
      loading: false,
      birthIcon: '<span class="iconfont icon-shengri"></span>',
      userInfo: {},
      form: {
        userId: '',
        userName: '刘勇',
        birthday: '',
        userSex: ''
      }
    }
  },
  created () {
    const user = JSON.parse(this.$localStorage.get('userInfo'))
    console.log(user)
    this.form.userId = user.userId
    this.form.userName = user.userName
    this.form.birthday = user.birthday
    this.form.userSex = user.userSex
  },
  methods: {
    handleSubmit () {
      this.editInformation(this)
    },
    ...mapMutations({
      editInformation: 'editInformation'
    })
  },
  components: {
    XInput,
    Group,
    Datetime,
    XButton,
    Cell
  }
}



